import svg4everybody from 'svg4everybody';
import $ from 'jquery';
import 'fullpage.js';
import {TimelineMax} from 'gsap';
import '../../node_modules/wowjs/dist/wow.js';
import '../blocks/fullpage/fullpage.js';
import '../blocks/menu/menu.js';

$(() => {
	svg4everybody();
});

let tl = new TimelineMax();

tl	.staggerFromTo('.navigation', 0.7, {x:-200,opacity: 0})
	.fromTo('.logo', 0.5, {y:-200,opacity: 0}, {y: 0,opacity: 1})
	.fromTo('.burger', 1, {y:-200,opacity: 0}, {y: 0,opacity: 1})
	.fromTo('.header__intro', 1, {x:-200,opacity: 0}, {x: 0,opacity: 1})
	.fromTo('.header__media', 1, {x:200,opacity: 0}, {x: 0,opacity: 1});


